.welcome {
  width: 100%;
  height: 100%;
}
.logo {
  padding-top: 1.5rem;
  width: 250px;
  height: 250px;
}
.showcase {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  height: 40vh;
  padding: 1.8rem;
}
.info {
  text-align: start;
  padding: 1.8rem;
  /*width: 750px;*/
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.header {
  font-size: 90px;
  color: #333;
  margin: 0;
  display: flex;
  flex-direction: column;
}
.text {
  width: 750px;
  font-size: 1.3rem;
  color: #333;
  letter-spacing: 0.8px;
}
/* shame */
.h_text {
  font-size: 1.3rem;
  color: #333;
  letter-spacing: 0.8px;
  padding: 1rem 0;

  width: 390px;
}
.button {
  font: inherit;
  border: 1px solid #f0db4f;
  background: #f0db4f;
  color: #333;
  padding: 0.75rem 3.5rem;
  cursor: pointer;
  font-size: 1.15rem;
  border-radius: 30px;
  transition: all 0.1s ease-in-out;
  position: relative;
  top: 2rem;
  margin: 0 auto;
}

.button:hover,
.button:active {
  background: #d5ba08;
  border-color: #d5ba08;
}

.button:focus {
  outline: none;
}

button:disabled,
button:focus:disabled,
button:hover:disabled,
button:active:disabled {
  background: #ccc;
  border-color: #ccc;
  color: #666666;
  cursor: not-allowed;
}
@media (max-width: 480px) {
  .header {
    font-size: 2rem;
    text-align: center;
  }
  .logo {
    width: 100px;
    height: 100px;
    padding: 1.5rem;
  }
  .h_text {
    width: 100%;
  }
  .showcase {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column-reverse;
  }
  .info {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 1rem;
    height: 100%;
    text-align: center;
    font-size: 0.5rem;
  }
  .info_wrapper {
    width: 100%;
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .text {
    font-size: 1rem;
    padding: 1.5rem;
    width: 100%;
    text-align: justify;
    letter-spacing: 0px;
  }
  .button {
    padding: 0.5rem 1rem;
    margin: 0.9rem;
    top: 0;
  }
}
