.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}
.final {
  width: 450px;
  height: 450px;
}

.final_header {
  font-size: 2.6rem;
  color: #333;
  border-bottom: 2px solid #f4f4f4;
  padding: 1rem;
}
.final_text {
  color: #333;
  background: #f4f4f4;
  padding: 2.2rem;
  border-radius: 15px;
}

.header {
  display: flex;
  height: 20%;
  gap: 5rem;
  justify-content: center;
  align-items: center;
}
.percentage {
  font-size: 5.5rem;
  color: #333;
  color: #f0db4f;
  display: flex;
  width: 200px;
}
.restart {
  background: #f4f4f4;
  font-size: 1.3rem;
  color: #f0db4f;
  outline: none;
  border: none;
  padding: 0.8rem;
  border-radius: 25px;
  cursor: pointer;
}

.restart:hover {
  background: #f0db4f;
  color: #f4f4f4;
  transition: all 0.1s ease-in-out;
}
@media (max-width: 480px) {
  .wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .restart {
    margin-top: 5rem;
  }
  .final_header {
    font-size: 2rem;
    text-align: center;
  }
  .header {
    flex-direction: column-reverse;
    gap: 2rem;
    max-height: 700px;
  }
  .percentage {
    font-size: 4rem;
    text-align: center;
    margin-left: auto;
    height: 30%;
  }
  .final_text {
    padding: 0.7rem;
  }
  .final {
    width: 350px;
  }
}
